import { request } from '../../request.js'
export default {
	data() {
		return {
			faceList: {
				currentIndex: 0,
				list:[]
			}, 
			// 商品id
			id: '',
			// 充值分类
			classify: {
				currentIndex: 0,
				list: []
			},
			// 充值商品信息
			typeList: {},
			goods_id: '',
			// 用户输入的充值手机号码
			phone: ''
		};
	},
	onLoad(options) {
		this.id = options.id;
		this.getDataList();
		
	},
	methods: {
		// 选择充值类型
		chooseFaceType(index) {
			this.classify.currentIndex = index;
			this.getDataList()
		},
		// 选择充值面额
		chooseFacePay(index) {
			this.faceList.currentIndex = index;
			this.goods_id = this.faceList.list[index].id;
			console.log(this.goods_id);
		},
		// 请求数据
		getDataList() {
			uni.showToast({
				title: '数据加载中',
				icon: 'loading'
			});
			request({
				url: 'index.php?s=/wap/index/getMediaGoods',
				method: 'post',
				data: {
					media_id: this.id
				}
			}).then(res => {
				uni.hideToast()
				// 充值商品信息
				res.data.media.media_logo = this.$api + res.data.media.media_logo;
				this.typeList = res.data.media;
				console.log(this.typeList);
				// 充值分类(默认选择第一项)
				this.classify.list = res.data.cat_list;
				// 当前选中的充值分类id
				let id = this.classify.list[this.classify.currentIndex].id;
				// 刷选该分类下对应的商品
				this.faceList.list = res.data.goods_list.filter(item => {
					if(item.cat_id == id){
						return item
					}
				})
				this.goods_id = this.faceList.list[0].id
			})
		},
		// 获取用户输入的手机号
		getUserPhone(event) {
			this.phone = event.detail.value;
		},
		// 校验手机号码
		checkPhone() {
			if(this.typeList.user_type == 0) {
				if(!(/^1[3456789]\d{9}$/.test(this.phone))) {
					uni.showToast({
						title: '请输入正确手机号码',
						icon: 'none'
					});
					return false
				}
				else {
					return true
				}
			}
		},
		// 获取用户输入的QQ
		getUserQQ(event) {
			this.QQ = event.detail.value;
		},
		// 校验QQ
		checkQQ() {
			if(this.typeList.user_type == 1) {
				if(!(/^[1-9]\d*$/.test(this.QQ))) {
					uni.showToast({
						title: '请输入正确qq',
						icon: 'none'
					});
					return false
				}
				else {
					return true
				}
			}
		},
		// 付款页面
		goVideoCheckstand() {
			request({
				url:"index.php?s=/wap/member/checkLogin",
				method: 'post'
			}).then(res => {
				if(res.data == "") {
					if(this.checkPhone() || this.checkQQ()) {
						// 如果是手机号充值
						if(this.typeList.user_type == 0) {
							uni.navigateTo({
								url: '/pages/components/video-checkstand/video-checkstand?id='+this.goods_id+'&mobile='+this.phone,
							})
						}
						// 如果是QQ号充值
						if(this.typeList.user_type == 1) {
							uni.navigateTo({
								url: '/pages/components/video-checkstand/video-checkstand?id='+this.goods_id+'&mobile='+this.QQ,
							})
						}
						
					}
					
				}
				else {
					uni.showModal({
						title: '请先登录!',
						content:'是否前往登录页面?',
						success: res => {
							if(res.confirm) {
								uni.navigateTo({
									url: '/pages/components/login/login'
								})
							}
							else {}
						}
					})
				}
			})
			// uni.navigateTo({
			// 	url: '/pages/components/video-checkstand/video-checkstand?id='+id
			// })
			
			
		}
	}
}
